<template>
  <div class="container" ref="containerRef">
    <!-- 索引条 -->
    <div class="index-bar" :style="[indexBarStyle, dynamicIndexBar]">
      <div
        class="index-cell"
        v-for="rowCount in numbersOfRows"
        :key="rowCount"
        :style="[indexCellStyle]"
      >
        {{ rowCount }}
      </div>
    </div>
    <!-- 座位区域 -->
    <div class="seats" ref="seatsRef">
      <!-- 行 -->
      <div
        class="row"
        v-for="rowIndex in numbersOfRows"
        :key="rowIndex"
        :style="[rowStyle]"
      >
        <!-- 格 -->
        <div
          class="cell"
          v-for="cellIndex in maxNumbersOfCells"
          :key="cellIndex"
          :style="[cellStyle]"
        >
          <!-- 座位 -->
          <Seat
            v-if="
              seatingChart.seats.some((item) => {
                return (
                  String(item.rowNum) === String(rowIndex) &&
                  String(item.columnNum) === String(cellIndex)
                )
              })
            "
            :info="
              seatingChart.seats.filter((item) => {
                return (
                  String(item.rowNum) === String(rowIndex) &&
                  String(item.columnNum) === String(cellIndex)
                )
              })
            "
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed, nextTick, ref, toRefs } from 'vue'
import { hammerIt, hammerObj } from '@/utils/gestuter.js'
import Seat from '@/components/cinema_components/Seat.vue'

// eslint-disable-next-line no-undef
const props = defineProps({
  seatingChart: Object,
})

const seatsRef = ref(null)
const containerRef = ref(null)
// const indexBarRef = ref(null)

// 横排的总数量（矩阵高度）
const { height: numbersOfRows, width: maxNumbersOfCells } = toRefs(
  props.seatingChart
)

// 横排样式
const rowStyle = ref(null)
// 每个格子的样式
const cellStyle = ref(null)
// 索引条样式
const indexBarStyle = ref(null)
// 单元格尺寸
const cellSize = ref(null)
// 索引条宽度
const indexBarWidth = ref(20)

// watch([seatsRef, containerRef], ([seatsRef, containerRef]) => {
//   cellSize.value =
//     document.documentElement.clientWidth / maxNumbersOfCells.value - 4

//   hammerIt(seatsRef, containerRef)

//   rowStyle.value = {
//     width: cellSize.value * maxNumbersOfCells.value + 'pX',
//     height: cellSize.value + 4 + 'pX',
//   }
//   cellStyle.value = {
//     width: cellSize.value + 'pX',
//     height: cellSize.value + 'pX',
//   }
//   indexBarStyle.value = {
//     width: indexBarWidth.value + 'pX',
//     height: (cellSize.value + 4) * numbersOfRows.value + 'pX',
//     lineHeight: cellSize.value + 'pX',
//   }
// })

nextTick(() => {
  cellSize.value =
    document.documentElement.clientWidth / maxNumbersOfCells.value - 4

  hammerIt(seatsRef.value, containerRef.value)

  rowStyle.value = {
    width: cellSize.value * maxNumbersOfCells.value + 'pX',
    height: cellSize.value + 4 + 'pX',
  }
  cellStyle.value = {
    width: cellSize.value + 'pX',
    height: cellSize.value + 'pX',
  }
  indexBarStyle.value = {
    width: indexBarWidth.value + 'pX',
    height: (cellSize.value + 4) * numbersOfRows.value + 'pX',
    lineHeight: cellSize.value + 'pX',
  }
})

// 获取当前浏览器支持的transform兼容写法

function getTransfrom() {
  // document.createElement() 创建元素节点，
  const divStyle = document.createElement('div').style // 返回的是一组object对象，里面包含了transform
  // 可能涉及到的几种兼容性写法，通过循环，找出浏览器识别的那个
  // 依次为： 全部、Safari 和 Chrome、firefox、ie9、opera
  const transformArr = [
    'transfrom',
    'webkitTransform',
    'MozTransform',
    'msTransform',
    'OTransform',
  ]
  for (var i = 0; i < transformArr.length; i++) {
    if (transformArr[i] in divStyle) {
      // 找到以后立刻返回，结束函数
      return transformArr[i]
    }
  }
  // 如果没有找到，就直接返回空字符串
}

const dynamicIndexBar = computed(() => {
  // 缩放不变
  // return `transform: translate(10pX, ${hammerObj.value[5]}pX) scaleY(${hammerObj.value[3]})`
  // 高度不变
  // return `transform: translate(10pX, ${hammerObj.value[5]}pX) ;height: ${
  //   (cellSize.value + 4) * numbersOfRows.value * hammerObj.value[3]
  // }pX`
  return `${getTransfrom()}: translate(10pX, ${hammerObj.value[5]}pX) scaleY(${
    hammerObj.value[3]
  });`
})

const indexCellStyle = computed(() => {
  // 缩放不变
  // return `transform: scaleY(${1 / hammerObj.value[3]})`
  // 高度不变
  // return `height: ${20 * hammerObj.value[3]}pX;`
  return `${getTransfrom()}: scaleY(${1 / hammerObj.value[3]});`
})
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100%;
  background-color: #f4f4f4;
  z-index: 9;
  overflow: hidden;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.index-bar {
  position:absolute;
  background-color: rgba(141, 141, 141, 0.493);
  z-index: 99;
  border-radius: 10pX;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
}

.index-cell{
  flex: 1;
  width: 100%;
  text-align: center;
  font-size: 8px;
  background-color: red;
}
.seats { 
  position: absolute;          
  display: block;       
}
.row {
  box-sizing: border-box;
  white-space: nowrap;
}
.cell {
  display: inline-block;
  vertical-align: top;
  margin: 2pX;
}
</style>